$primary-color : rgb(87, 209, 215);

.header-container{
    display: flex;
    flex-direction: row;
    width: 100vw;
    height: 93vh;
    background: rgb(0,0,0);
background: linear-gradient(90deg, rgba(0,0,0,1) 56%, rgba(87,209,215,1) 100%);
  
    

    .header-img{
        position: absolute;
        left: 50vw;
        bottom: -25px;

       
    }

    .header-info{
        position: absolute;
        top:30vh;
        left: 10vw;
        padding: 30px;
        color:white;
        // text-align: center;
        // border: 1px solid $primary-color;
        border-radius: 20px;

        h2{
            font-family: 'Anton', cursive;
            font-size: 40px;
        }

        
      

        img{
            width: 40px;
            height: 40px;
            margin-top: 20px;
            margin-right: 20px;
            border-radius: 20px;
            filter: grayscale(1);
            transition: 1s ease;
            cursor: pointer;
            
        }

        img:hover{
            transform: scale(1.2);
            filter: brightness(1.4);
        }

        .header-info-text{
           
            font-size: 100px;
            font-family: 'Anton', cursive;
            letter-spacing: 4px;
            

        }

        .header-info-text2{
            color: $primary-color;
            font-size: 20px;
        }

        .header-button{
            width: 150px;
            height: 35px;
            margin-top:15px;
            font-size:20px;
            border-radius: 15px;
            background-color: transparent;
            color:white;
            border: 2px solid white;
            transition: 1s ease;
            cursor: pointer;

        }

        .header-button:hover{
            background-color: white;
            color:black;
        }
    }
}